import React from "react";
import DetailPresenter from "./DetailPresenter";
import { movieApi, tvApi } from "../../api";

export default class extends React.Component {
  constructor(props) {
    super(props);
    console.log("constructor!!");
    const {
      location: { pathname },
    } = props;

    this.state = {
      result: null,
      error: null,
      loading: true,
      isMovie: pathname.includes("/movie/"),
    };
  }

  async componentDidMount() {
    console.log("Did Mount !! ");
    // console.log(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps) {
    const {
      match: {
        params: { id: prevId },
      },
    } = prevProps;
    const {
      match: {
        params: { id: nowId },
      },
    } = this.props;
    console.log(prevId, nowId);
    if (prevId !== nowId) {
      this.getData();
    }
  }
  getData = async () => {
    const {
      match: {
        params: { id },
      },
      history: { push },
    } = this.props;

    const { isMovie } = this.state;
    const parsedIntId = parseInt(id);
    if (isNaN(parsedIntId)) {
      console.log("Hello Buddy");
      return push("/");
    }
    let result = null;
    try {
      if (isMovie) {
        ({ data: result } = await movieApi.movieDetail(parsedIntId));
      } else {
        ({ data: result } = await tvApi.showDetail(parsedIntId));
      }
    } catch {
      // If api couldn't get data
      // ì„ì‹œë¡œ
      return push("/");
      this.setState({ error: "Can not find anything" });
    } finally {
      this.setState({
        loading: false,
        result,
      });
    }
  };
  changeParam = (id) => {
    const {
      history: { push },
    } = this.props;
    push(`/movie/${id}`);
  };
  render() {
    const { result, error, loading, isMovie } = this.state;
    console.log("render -- 0 -- ");
    return (
      <DetailPresenter
        result={result}
        error={error}
        loading={loading}
        isMovie={isMovie}
        changeParam={this.changeParam}
      />
    );
  }
}
